@import "../../variables";

.skill-round {
  font-weight: lighter;
  font-size: calc(100vw / 100);
  text-transform: uppercase;
  color: white;
  letter-spacing: 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  row-gap: 2vh;
  width: calc(100% / 6);
  text-align: center;
  height: 100%;

  .round {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: end;
	.id {
	  width: 4vw;
	  height: 4vw;
	  border-radius: 5vw;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  font-size: calc(100vw / 60);
	  font-weight: 900;
	  color: $gray;
	}
  }

  .skill-title {
	height: calc(12.5vh - 2vh);
	display: flex;
	flex-direction: column;
	align-items: center;

  }

}